.recent-post {
  font-size: 13px;

  ul {
    line-height: 1.8em;
    padding-left: 10px;
    list-style: none;
  }

  > li {
    margin: 4px 0;
  }

  .recent-post-title {
    font-size: 16px;
  }

  .recent-post-list {
    padding-left: 0;
  }

  a.toc-link.active {
    color: #111;
    font-weight: bold;
  }

  a {
    color: #888;

    &:hover {
      color: darken(#888, 10%);
    }
  }
}

@media screen and (max-width: 1024px) {
  .recent-post {
    display: none;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1025px) {
  .recent-post {
    display: none;
  }
}